import React, { useState, useRef } from "react";
import MaterialTable, { MTableToolbar } from "material-table";
import { forwardRef } from "react";
import "semantic-ui-css/semantic.min.css";
import AddBox from "@material-ui/icons/AddBox";
import ArrowUpward from "@material-ui/icons/ArrowUpward";
import Check from "@material-ui/icons/Check";
import ChevronLeft from "@material-ui/icons/ChevronLeft";
import ChevronRight from "@material-ui/icons/ChevronRight";
import Clear from "@material-ui/icons/Clear";
import DeleteOutline from "@material-ui/icons/DeleteOutline";
import Edit from "@material-ui/icons/Edit";
import FilterList from "@material-ui/icons/FilterList";
import FirstPage from "@material-ui/icons/FirstPage";
import LastPage from "@material-ui/icons/LastPage";
import Remove from "@material-ui/icons/Remove";
import SaveAlt from "@material-ui/icons/SaveAlt";
import Search from "@material-ui/icons/Search";
import ViewColumn from "@material-ui/icons/ViewColumn";
import "react-toastify/dist/ReactToastify.css";
import "react-widgets/dist/css/react-widgets.css";
import "@reach/menu-button/styles.css";
import Moment from "moment";
import momentLocalizer from "react-widgets-moment";
import { useQuery } from "react-query";
import { getKpPrevpatientsKey } from "../../utils/queryKeys";
import Button from "@material-ui/core/Button";
import { queryClient } from "../../utils/queryClient";
import { fetchKpPrevPatients } from "../../services/fetchKpPrevPatients";
import { MdDashboard } from "react-icons/md";
import { ButtonGroup } from "reactstrap";
import { Link } from "react-router-dom";

//Date Picker package
Moment.locale("en");
momentLocalizer();

const tableIcons = {
  Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
  Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
  Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
  DetailPanel: forwardRef((props, ref) => (
    <ChevronRight {...props} ref={ref} />
  )),
  Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
  Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
  Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
  FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
  LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
  NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  PreviousPage: forwardRef((props, ref) => (
    <ChevronLeft {...props} ref={ref} />
  )),
  ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
  SortArrow: forwardRef((props, ref) => <ArrowUpward {...props} ref={ref} />),
  ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
  ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />),
};

const KpPrevPatientList = (props) => {
  const [query, setQueryParams] = useState({
    page: 0,
    pageSize: 10,
    search: "",
  });

  const innerContainerRef = useRef(null);

  // Function to scroll to the top of the inner container
  const scrollToTop = () => {
    const outerContainer = document?.getElementById?.("main-wrapper");

    if (outerContainer) {
      outerContainer.scrollTop = 0;
    }
  };

  const prefetchNextPage = async () => {
    const nextPage = query.page + 1;
    // Use the same query key as in the useQuery hook
    const queryKey = [getKpPrevpatientsKey, { ...query, page: nextPage }];
    await queryClient.prefetchQuery(queryKey, () =>
      fetchKpPrevPatients({ ...query, page: nextPage })
    );
  };

  const { data, isLoading, refetch } = useQuery(
    [getKpPrevpatientsKey, query],
    () => fetchKpPrevPatients(query),
    {
      onSuccess: () => {
        prefetchNextPage();
      },
    }
  );

  return (
    <div ref={innerContainerRef}>
      <MaterialTable
        icons={tableIcons}
        title="Find Patient"
        columns={[
          {
            title: "Date Service Offered",
            field: "dateServiceOffered",
            render: (row) => row.dateServiceOffered,
          },
          {
            title: "Hospital Number",
            field: "hospital_number",
            filtering: false,
            render: (row) =>
              row.htsCode !== null ? row.htsCode : row.prepCode,
          },

          {
            title: "Prevention Code",
            field: "prevCode",
            filtering: false,
          },

          {
            title: "Actions",
            field: "actions",
            filtering: false,
            render: (row) => (
              <div>
                <Link
                  to={{
                    pathname: "/patient-history",
                    state: { patientObj: row },
                  }}
                >
                  <ButtonGroup
                    variant="contained"
                    aria-label="split button"
                    style={{
                      backgroundColor: "rgb(153, 46, 98)",
                      height: "30px",
                      width: "215px",
                    }}
                    size="large"
                  >
                    <Button
                      color="primary"
                      size="small"
                      aria-label="select merge strategy"
                      aria-haspopup="menu"
                      style={{ backgroundColor: "rgb(153, 46, 98)" }}
                    >
                      <MdDashboard />
                    </Button>
                    <Button style={{ backgroundColor: "rgb(153, 46, 98)" }}>
                      <span
                        style={{
                          fontSize: "10px",
                          color: "#fff",
                          fontWeight: "bolder",
                        }}
                      >
                        Patient Dashboard
                      </span>
                    </Button>
                  </ButtonGroup>
                </Link>
              </div>

              //   <div>
              //   <Link
              //     to={{
              //       pathname: "/view-kp-prev",
              //       state: { patientObj: row },
              //     }}
              //   >
              //     <ButtonGroup
              //       variant="contained"
              //       aria-label="split button"
              //       style={{
              //         backgroundColor: "rgb(153, 46, 98)",
              //         height: "30px",
              //         width: "215px",
              //       }}
              //       size="large"
              //     >
              //       <Button
              //         color="primary"
              //         size="small"
              //         aria-label="select merge strategy"
              //         aria-haspopup="menu"
              //         style={{ backgroundColor: "rgb(153, 46, 98)" }}
              //       >
              //         <MdDashboard />
              //       </Button>
              //       <Button
              //         style={{ backgroundColor: "rgb(153, 46, 98)" }}
              //       >
              //         <span
              //           style={{
              //             fontSize: "12px",
              //             color: "#fff",
              //             fontWeight: "bolder",
              //           }}
              //         >
              //           View KP Prev
              //         </span>
              //       </Button>
              //     </ButtonGroup>
              //   </Link>
              // </div>
            ),
          },
        ]}
        components={{
          Toolbar: (props) => (
            <div>
              <MTableToolbar {...props} />
            </div>
          ),
        }}
        data={data?.records || []}
        onQueryChange={scrollToTop()}
        totalCount={data?.totalRecords}
        isLoading={isLoading}
        page={data?.currentPage}
        options={{
          headerStyle: {
            backgroundColor: "#014d88",
            color: "#fff",
          },
          searchFieldStyle: {
            width: "200%",
            margingLeft: "250px",
          },
          filtering: false,
          paging: true,
          exportButton: false,
          searchFieldAlignment: "left",
          pageSizeOptions: [10, 20, 100],
          pageSize: query?.pageSize || 10,
          debounceInterval: 400,
        }}
        onChangePage={(newPage) => {
          setQueryParams((prevFilters) => ({ ...prevFilters, page: newPage }));
          refetch(query);
        }}
      />
    </div>
  );
};

export default KpPrevPatientList;
